.aboutuscontant{
    margin-left: 30px;

}
.lorem{
    font-size: 12px;
    font-weight: bolder;
    color: rgb(190, 190, 190);
    margin-top: -24px;
    margin-left: -18px;
}
.box-container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 40px;
    margin-left: 50px;
    margin-right: 50px;
  
}
.box{
    border: 1px solid blueviolet;
    width: 260px;
    border-radius: 0 15px;
    margin-right: 30px;
    height: auto;
    background-image: linear-gradient(to right, rgb(107, 204, 243), white);
  
}
.box h1,h2,p{
    text-align: left;
    padding-left:20px;
}
.box h1{
        font-size: 50px;
        margin-top: 4px;
        background: linear-gradient(to right, #5470ec 10% , #340074 20% );
        background-clip: text;
        -webkit-text-fill-color: transparent; 
}
.box h2{
    font-size: 40px;
    color: rgb(31, 31, 31);
    margin-top: -35px;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}
.box p{
    color: gray;
    margin-top: -30px;
}

/* latesr blogs */
.latestBlog{
    background-color: #000000;
    margin-top: 30px;
    width: 100%;
    height: auto;
    padding-bottom: 50px;
    padding-top: 30px;
    margin-right: 30px;
}
.row1{
    display: flex;
    justify-content: space-evenly;
    align-items: center;    
    margin-top: 30px;

}
.row1 h1{
    color: white;
    width: 400px;
}
.row1 h1 span{
    background: linear-gradient(to right, #4262f0 , #32006e );
    background-clip: text;
    -webkit-text-fill-color: transparent; 
}

.row1 p{
    color: gray;
    font-size: 13px;
    width: 300px;
}

.show-products{
    border: 1px solid gray;
    border-radius: 0 30px;
    color: gray;
    padding: 8px 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
}
.show-products:hover{
    background-image: linear-gradient(to right, #4262f0 , #32006e );
    color: white;
    
}
.show-products span{
    margin-right: 10px;
}

.blog-img{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    margin-left: 60px;
    margin-top: 20px;
    
}
.blog-img-container img{
    width: 550px;
    height: auto;
}
.blog-img-container h1{
    color: white;
}
.blog-img-container h1:hover{
    background: linear-gradient(to right, #7b93fd 30% , #610186 50% );
     background-clip: text;
    -webkit-text-fill-color: transparent; 
}
.times{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: -10px;
}
.times .commend{
    color: rgb(158, 158, 158);
    margin-right: 40px;
}
.qwerty{
    color: gray;
    font-size: 14px;
    margin-right: 30px;
}
.read{
    background-image: linear-gradient(to right, rgb(88, 157, 248), rgb(94, 2, 180));
    width: fit-content;
    padding: 5px 20px;
    color: white;
    border-radius: 0 15px;
    margin-top: 20px;
}

/* next box  */

.keycontainer{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
   /* margin-left: -30px; */
    margin-left: 24px;
    margin-top: 30px;
    /* margin-right: 30px; */
    
   
}
.keycontainer .keybox{
    width: 270px;
    height: auto;
    /* border: 1px solid purple ; */
    border-radius: 0 10px;
    padding:2px;
    /* background-image: linear-gradient(rgb(195, 255, 252), rgb(255, 255, 248)); */
    background: linear-gradient(rgb(114, 214, 253), rgb(232, 233, 245)), 
    linear-gradient(to right, rgb(0, 188, 245), purple);
    background-origin: padding-box, border-box;
    background-repeat: no-repeat; /* this is important */
    border: 1px solid transparent;
}
.keybox:hover{
    box-shadow:1px 1px 10px 2px  rgb(245, 156, 245);
}

.keybox .keyLogo{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 260px;
}
.keyLogo p{
    background-image: linear-gradient(to right, rgb(0, 195, 255) ,rgb(245, 245, 245) ) ;
    text-align: left;
    padding:2px 10px;
    border-radius:3px;
    margin-left: 10px;
    color: gray;
    font-size: 12px;
}
.keybox .keytext{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 265px;
    font-size: 25px;
    margin-left: -10px;
    margin-top: -20px;
}
.keytext p{
    font-size: 25px;
    font-weight:bolder;
}
.keybox img{
    width: 240px;
    height: auto;
    margin-top:-20px;
    /* border: 2px solid black; */
    transition: 400ms;
}
.keybox:hover img{
    transform: scale(1.2);
}
.selectStar span{
    font-size:8px;
    color: red;
    font-weight: bolder;   
}
.keybox select{
    border: 1px solid gray;
    width: 90%;
    padding: 9px 10px;
    outline: none;
    border-radius: 0 10px;
    margin-top: 10px;
    margin-left: 10px;
    color: gray;
}
.keybox select option{
    color: gray;
}

.keyPrice{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-right: 20px;
}

.keyPrice p{
    font-size: 24px;
    font-weight: bolder;
}
.keyPrice a{
    text-decoration: none;
    background-image: linear-gradient(to right, rgb(88, 157, 248), rgb(94, 2, 180));
    color: white;
    font-size: 14px;
    font-weight: bolder;
    padding: 8px 15px;
    border-radius: 0 10px;
}
.keyPrice a:hover{
    background: black;
}

.btnad{
    background-image: linear-gradient(to right, rgb(88, 157, 248), rgb(94, 2, 180));
    color: white;
    font-size: 14px;
    font-weight: bolder;
    padding: 8px 15px;
    border-radius: 0 10px;
    border: none;
}
.btnad:hover{
    background: black;
}
.btnRemove{
    background-image: linear-gradient(to right, rgb(250, 11, 43), rgb(252, 201, 201));
    color: white;
    font-size: 10px;
    font-weight: bolder;
    padding: 8px 15px;
    border-radius: 0 10px;
    border: none;
}
