
.first{
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 0.5px solid gray;
    /* background-color: black; */
}
.gamingicons{
    display: flex;
    justify-content: space-between;
    width: 70px;
    font-size: 22px;
    color: white;
}
.search{
    border:1px solid gray ;
    padding: 7px 15px ;
    border-radius: 0px  10px;
    color: gray;
    background-color:rgb(16,1, 31);
}
.search input{
    border: none;
    outline: none; 
    padding: 0 10px;
    background-color:rgb(16,1, 31);
    color: white;
}
.gaminglogo{
    color: whitesmoke;
}
.head{
    background-image: url("https://opencart.workdo.io/gaming/image/cache/catalog/slider/slider-1920x715.jpg");
    background-position: top;
    /* padding-left: 500px ; */
    width:1674px;
    padding-right: 12px;
    height: 100vh;
    background-repeat: no-repeat;
    background-size: cover ;
    
}


/* Featured */
.bestpro{
    margin-left: 60px;
    margin-top: 70px;
}

.featured{
    background-color: rgb(43, 32, 92);
    width: fit-content;
    padding: 10px 30px;
    border-radius: 20px;
}
.collector1{
    color: rgb(163, 160, 160);
    font-size: 13px;
    font-weight: bolder;
    background-image: linear-gradient(to right, rgb(59, 57, 184) , rgb(17, 2, 82));
    padding: 3px 10px;
    border-radius:10px;
}
.collector2{
    font-size: 13px;
    font-weight: bolder;
    color: white;
    padding: 0px 10px;
}
.collector3{
    font-size: 13px;
    font-weight: bolder;
    color:rgb(83, 80, 247);
}
.pro{
    font-size: 60px;
    color: white;
    margin-top: 20px;
}
.pro .progame{
   /* background:linear-gradient(to right, rgb(59, 57, 184) , rgb(17, 2, 82)); */
   background: linear-gradient(to right, #304dd0 60%, #330867 100%);
   -webkit-background-clip: text;
   -webkit-text-fill-color: transparent; 
}
.acc{
   position: absolute;
   top: 310px;
}
.para{
    width: 450px;
    color: rgb(117, 117, 117);
    font-size: 12px;
    font-weight: bolder;
    margin-top: 90px;
    margin-left: -19px;
}
 
