.fcontainer{
    background-image: url("data:image/jpeg;base64,");
    padding-bottom: 40px;
    background-position: right;
    background-repeat:repeat-y;
    background-size:cover;
    width:1681px;
}
.fcontant{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-left: 40px;
    margin-right: 40px;
    padding-top: 20px;
   
}
.fcon .sub{
    background-image: linear-gradient(to right, rgb(88, 157, 248), rgb(94, 2, 180));
    color: white;
    font-size: 14px;
    font-weight: bolder;
    padding: 8px 15px;
    border-radius: 0 10px;
    width: fit-content;
}
.fcon h1{
    color: white;
}
.fcon h1 span{
    background: linear-gradient(to right, #304dd0 60%, #330867 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
.fcon .fpara{
    color: gray;
    font-size: 14px;
}
.fcon .subInput{
    border: 1px solid rgb(95, 94, 94);
    background-color: none;
    border-radius: 0 10px;
    background-color: #330867;
}
.fcon .subInput:hover{
    border-color: white;
}
.subInput input{
    width: 74%;
    height: 100%;
    border: none;
    outline: none;
    padding: 15px 10px;
    font-weight: bolder;
    color: rgb(255, 255, 255);
    background-image: linear-gradient(to right, rgb(0, 0, 17), rgb(14, 14, 44));
    border-radius: 0 0 0 10px;
}
input::placeholder {
    color: rgb(255, 255, 255);
    opacity: 1; /* Firefox */
  }
.subInput a{
    text-decoration: none;
    background-image: linear-gradient(to right, rgb(88, 157, 248), rgb(94, 2, 180));
    color: white;
    font-size: 14px;
    font-weight: bolder;
    padding: 14px 25px;
    border-radius: 0 10px;
    width: fit-content;

}
/* .subInput a:hover{
    background-color: black;
    color: white;
} */
.fcon  .copy{
    color: rgb(255, 255, 255);
    font-size: 14px;
    margin-top:50px;
}

.fcon ul{
    list-style-type:none;
    display: block;
    /* background-color: wheat; */
}
.fcon ul li{
    padding: 3px 0;
}
.fcon ul li .fnav{
    color: white;
    text-decoration: none;
    font-size: 12px;
    font-weight: bolder;
}
.fcon ul li .fnav:hover{
    background: linear-gradient(to right, #3a5df7 60%, #6306d4 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.fhead{
    color: white;
    font-weight: bold;
    margin-top: -20px;
    font-size: 18px;
}
.icon-social{
    color: rgb(0, 0, 0);
    font-size: 16px;
    padding: 10px 5px;
    background-color: white;
    margin: 8px;
    text-align: center;
    border-radius: 8px;
    cursor: pointer;
}
   
.icon-social:hover{
    background-image: linear-gradient(to right, rgb(88, 157, 248), rgb(94, 2, 180));
    color: white;
}
.ficons{
    transition: 200ms;
}
.icon-social:hover .ficons{
    transform: scale(1.7);
}