.aform-container{
    display: flex;
    justify-content: center;
    align-items: center;
    /* background-image: linear-gradient(to right , white , blue); */
    background-image: url("data:image/jpeg;base64,");
    background-position: right;
    background-repeat:repeat-y;
    background-size:cover;
}
.aform{
    width: 60%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.head-add{
    background: linear-gradient(to right, #1f43e2  , #1d0041 );
    background-clip: text;
    -webkit-text-fill-color: transparent; 
    font-weight:bolder;
}
.rooms{
    border-right: 3px solid rgb(47, 24, 250);
    padding-right: 60px;
}
.aname{
    width:500px;
    padding: 5px 20px;
    margin-bottom: 20px;
    border: none;
    outline: none;
    background: linear-gradient(to right, #294be0  , #260252 );
    border-bottom: 2px solid purple;
    color: white;
    font-size: 16px;
}
.aname::placeholder{
    color: white;
}
.spanmsg{
    color: green;
    font-weight: bold;
}
.orderbtn{
    width: 80% ;
    margin-left: 40px;
    margin-top: 30px;
    margin-bottom: 40px;
    padding: 5px 0;
    border-radius: 10px;
    font-size: 16px;
    border: none;
    color: white;
    background: linear-gradient(to right, #1f43e2  , #1d0041 );
}


.rowaa h3{
    background: linear-gradient(to right, #1f43e2  , #1d0041 );
    background-clip: text;
    -webkit-text-fill-color: transparent; 
    font-weight: bold;
    font-size: 22px;
}
h3 span{
    color: green;
}

.vls{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.vls p{
    font-size: 20px;
    color: rgb(241, 214, 238);
    font-weight: bold;
}
.qwertyyyy{
    width: 100%;
    padding: 5px 0;
    font-size: 20px;
    font-weight: bolder;
    background-color: rgb(82, 153, 82);
    border: none;
    color: white;
    border-radius: 20px;
}