.containerCart{
    background-image: url("data:image/jpeg;base64,");
    padding-bottom: 40px;
    background-position: right;
    background-repeat:repeat-y;
    background-size:cover;
    width: 100%;
    height:fit-content;
    
    
}
.cart-headings{
    background: linear-gradient(to right, #4262f0 1% , #260153 20%);
    background-clip: text;
    -webkit-text-fill-color: transparent; 
    font-size: 50px;
}
.cartContainer{
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin: 10px;
    flex-wrap: wrap;
}
.cartProduct{
   border-radius: 10px;
    width: 500px;
    margin-bottom: 20px;   
    background-image: linear-gradient(to right ,blue,skyblue); 
}
.cartProduct img{
    transition: 400ms;
    padding-left: 150px;
    padding-top: 20px;
}
.cartProduct:hover img{
    transform: scale(1.3);
}
.productDetails{
    background: linear-gradient(to right, #02166e , #260153 );
    background-clip: text;
    -webkit-text-fill-color: transparent; 
    font-size: 20px;
    width: 400px;
    padding-left: 50px;
}
.productName{
   color:white;
   font-size: 30px;
}
.priceProduct{
    background-color:white;
    width: 400px;
    align-items: center;
    margin-left: 50px;
    text-align: center;
    padding: 3px 0;
    font-size: 20px;
    border-radius: 30px;
    color: green;
    font-weight: bolder;
}
.cart-amt{
  font-size: 22px;
  color: white;
  text-align: center;
  background-image: linear-gradient(to left,blue,rgb(195, 0, 255));
  width:200px;
  margin-left: 730px;
  padding: 5px 20px;
  border-radius: 20px;
  margin-bottom: 20px;
}
.orderBtn{
    cursor: pointer;
    width: 50%;
    padding: 10px 0;
    border-radius: 30px;
    margin-left: 400px;
    font-size: 20px;
    font-weight: bolder;
    color: rgb(255, 255, 255);
    margin-bottom: 30px;
    background-image: linear-gradient(to left,blue,rgb(195, 0, 255));
}
